@charset "UTF-8";
/* reset css */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

/* rwd img css，可當作是 CSS Reset 其中一個 */
img {
  max-width: 100%;
  height: auto; }

.d-f {
  display: flex; }

.mt-21 {
  margin-top: 0; }

/* 全域 border box */
*, *::before, *::after {
  box-sizing: border-box; }

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 15 auto;
  line-height: 1.5; }

@media (max-width: 768px) {
  .container {
    max-width: 720px; } }

@media (max-width: 767px) {
  .container {
    max-width: 720px; } }

/* header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #AA0601;
  padding: 0 56px 0 88px; }

@media (max-width: 992px) {
  .header {
    padding: 0 7px 0 39px; } }

@media (max-width: 767px) {
  .header {
    flex-direction: column;
    padding: 0; } }

.logo a {
  display: block;
  width: 80px;
  height: 49px;
  margin: 30px 0;
  background: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/logo.png) center center no-repeat;
  background-size: cover;
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden; }

@media (max-width: 767px) {
  .logo a {
    width: 48px;
    height: 29px;
    margin: 10px 0; } }

.menu {
  display: flex; }
  .menu a {
    display: block;
    color: #ffffff;
    font-size: 20px;
    padding: 39px 32px; }
  .menu a:hover {
    color: #24c416; }

@media (max-width: 768px) {
  .menu a {
    padding: 39px 20px; } }

@media (max-width: 767px) {
  .menu {
    flex-wrap: wrap; }
    .menu li {
      width: 50%;
      border: #ffffff 1px solid;
      text-align: center;
      padding: 8px 52px; }
    .menu a {
      display: block;
      padding: 0; } }

/* footer */
.footer {
  background: #AA0601;
  padding: 44px 0 39px 0; }
  .footer_top {
    position: relative; }
  .footer_menu {
    display: flex;
    margin-bottom: 36px; }
  .footer_menu a {
    color: #ffffff;
    padding-right: 48px; }
  .footer_menu a:hover {
    color: #24c416; }

@media (max-width: 768px) {
  .footer {
    padding: 38px 0 32px 0; }
    .footer_menu {
      margin-bottom: 18px; }
    .footer_menu a {
      padding-right: 40px; } }

@media (max-width: 767px) {
  .footer {
    padding: 25px 0 24px 0; }
    .footer_menu {
      display: none; } }

.social {
  position: absolute;
  right: 0;
  top: 0; }
  .social a {
    padding-left: 32px; }
  .social img {
    height: 48px;
    width: 48px; }
  .social img:hover {
    background: #24c416; }

@media (max-width: 768px) {
  .social a {
    padding-left: 24px; }
  .social img {
    height: 24px;
    width: 24px; } }

.contact li {
  display: flex;
  align-items: center;
  font-size: 36px;
  margin-bottom: 16px; }

.contact img {
  height: 36px;
  width: 36px;
  margin-right: 22px; }

@media (max-width: 768px) {
  .contact li {
    font-size: 20px;
    margin-bottom: 12px; }
  .contact img {
    height: 18px;
    width: 18px;
    margin-right: 11px; } }

@media (max-width: 767px) {
  .contact li {
    font-size: 16px;
    margin-bottom: 6px; }
  .contact img {
    height: 15px;
    width: 15px;
    margin-right: 6.5px; } }

.contact a {
  color: #ffffff; }
  .contact a:hover {
    color: #24c416; }

.line {
  margin-top: 8px;
  margin-bottom: 32px;
  border-bottom: 1px solid #ffffff; }

@media (max-width: 768px) {
  .line {
    margin-bottom: 24px; } }

@media (max-width: 768px) {
  .line {
    margin-top: 16px;
    margin-bottom: 16px; } }

.copyright {
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .copyright_content {
    display: flex; }
  .copyright p {
    font-size: 14px;
    margin-left: 48px; }
  .copyright p:hover {
    color: #24c416; }

@media (max-width: 768px) {
  .copyright p {
    margin-left: 24px; } }

@media (max-width: 767px) {
  .copyright {
    display: block;
    font-size: 16px; }
    .copyright__content {
      display: block; }
    .copyright p {
      margin-left: 0;
      margin-top: 8px; } }

/* faq */
.faq {
  padding-top: 96px;
  padding-bottom: 128px; }
  .faq h2 {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 60px; }
  .faq h2::before {
    content: "";
    border-right: 8px solid #9F1213;
    margin-right: 16px; }
  .faq h3 {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 36px; }
  .faq h4 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 16px; }
  .faq_p {
    margin-left: 95px;
    margin-bottom: 32px; }

@media (max-width: 768px) {
  .faq {
    padding-top: 60px;
    padding-bottom: 72px; }
    .faq h2 {
      margin-bottom: 40px; }
    .faq h2::before {
      border-right: 4px solid #9F1213; }
  h3 {
    margin-bottom: 32px; }
  _p {
    margin-left: 59px;
    margin-bottom: 24px; } }

@media (max-width: 767px) {
  .faq {
    padding-top: 28px;
    margin-bottom: 36px; }
    .faq h2 {
      font-size: 28px;
      margin-bottom: 24px; }
    .faq h2::before {
      margin-right: 8px; }
    .faq h3 {
      font-size: 20px;
      font-weight: normal;
      margin-bottom: 24px; }
    .faq h4 {
      font-size: 16px;
      margin-bottom: 8px; }
    .faq_p {
      font-size: 14px;
      margin-bottom: 20px; }
  .mt-21 {
    margin-top: 21px; } }
